.player{
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.time-control {
    width: 50%;
    display: flex;
    align-items: center;
    input{
        width: 100%;
        -webkit-appearance: none;
        background-color: transparent;
        cursor: pointer;
    }
    p{
        padding: 1rem;
    }
}
.play-control {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem;
    font-size: 0.8rem;
    width: 30%;
    svg{
        cursor: pointer;
    }
}
input[type="range"]:focus{
    outline: none;
}
input[type="range"]::-webkit-slider-thumb{
    -webkit-appearance: none;
    height: 12px;
    width: 12px;
    
}

.track{
    background: lightblue;
    width: 100%;
    height: 0.6rem;
    position: relative;
    border-radius: 1rem;
    overflow: hidden;
}

.animate-track {
    background: rgb(204, 204, 204);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transform: translateX(0%);
    pointer-events: none;
 }

 @media screen and(max-width : 768px){
    .time-control{
        width: 90%;
    }
    .play-control{
        width: 60%;
    }
}